import { Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { playSound } from "./sound";

const sprite = {
    width: 512,
    height: 154,
    k: 512 / 154,
    screenK: 8,
    offsetK: 20
}

class LogoButton extends Sprite {
    constructor( screen, clickCallback ) {
        super( getTexture( textureKeys.marsGame ) )
        this.anchor.set( 0, 0 )
        this.resize(screen)
        this.eventMode = 'static';
        this.clickCallback = clickCallback

        this.on('pointertap', () => {
            playSound(soundKeys.click)
            this.clickCallback()
        })
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const size = screenSize / sprite.screenK
        this.height = size > sprite.height ? sprite.height : size
        this.width = this.height * sprite.k

        this.position.x = screenSize / sprite.offsetK
        this.position.y = screenSize / sprite.offsetK
    }
}

export default LogoButton