import { Container, Sprite, Text } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { textStyles } from './textStyles'
import { playSound } from "./sound";

const settings = {}
settings.imageSize = 251
settings.imageScreenK = 8
settings.imageOffsetK = 20

settings.textScreenK = 10
settings.textSize = (settings.imageSize * settings.imageScreenK) / settings.textScreenK

class Scores extends Container {
    constructor( screen, scores ) {
        super()
        this.image = new Sprite( getTexture(textureKeys.scores) )
        this.image.anchor.set( 0, 0 )
        this.addChild(this.image)
        this.text = new Text( scores, {...textStyles.scores, fontSize: settings.textSize} )
        this.addChild(this.text)
        this.resize(screen)
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const spriteSize = screenSize / settings.imageScreenK
        
        this.image.width = this.image.height = spriteSize > settings.imageSize ? settings.imageSize : spriteSize
        this.image.position.x = screenSize / settings.imageOffsetK
        this.image.position.y = screenSize / settings.imageOffsetK

        const textSize = screenSize / settings.textScreenK
        this.text.style.fontSize = textSize > settings.textSize ? settings.textSize : textSize
        const textOffset = (this.image.height - textSize) / 1.5
        this.text.position.x = textOffset + this.image.position.x + this.image.width
        this.text.position.y = textOffset + this.image.position.y
    }

    addScore( scores ) {
        playSound(soundKeys.true)
        this.text.text = scores
    }
}

export default Scores