import { Application, Container } from 'pixi.js'
import ProgressBar from './progressBar'
import StartButton from './startButton'
import { playMusic, stopMusic, startMusic } from './sound'
import { loadAssets } from './loader'
import LanguageButton from './languageButton'
import LogoButton from './logoButton'
import SoundButton from './soundButton'
import ResultsButton from './resultsButton'
import Background from './background'
import Slide from './slide'
import Scores from './scores'
import Attempts from './attempts'
import Controller from './controller'

const lang = navigator.language || navigator.userLanguage;
let isLangRu = !!(~lang.indexOf('ru'));

const app = new Application({
    background: '#ffffff',
    antialias: 'true', // 
    resolution: 2,
    resizeTo: window
    //width: window.innerWidth,
    //height: window.innerHeight,
})
document.body.append( app.view )

const screen = {}
resize()

const BG = new Container()
const SLIDES = new Container()
const TopUI = new Container()
const BottomUI = new Container()
const ControlUI = new Container()
/*
BG._NAME = 'BG'
SLIDES._NAME = 'SLIDES'
TopUI._NAME = 'TopUI'
BottomUI._NAME = 'BottomUI'
ControlUI._NAME = 'ControlUI'
*/

function clearContainer( container ) {
    while(container.children[0]) {
        removeSprite( container.children[0] )
    }
}

function removeSprite( sprite ) {
    sprite.parent.removeChild( sprite )
    sprite.destroy( {children : true} )
}

app.stage.addChild( BG )
app.stage.addChild( SLIDES )
app.stage.addChild( TopUI )
app.stage.addChild( BottomUI )
app.stage.addChild( ControlUI )

let orientation = window.matchMedia("(orientation: portrait)");
orientation.addEventListener("change", () => setTimeout(resize, 0))
window.addEventListener('resize', () => setTimeout(resize, 0))
window.addEventListener('focus', playMusic)
window.addEventListener('blur', stopMusic)

function resize() {
    screen.width = app.screen.width,
    screen.height = app.screen.height,
    screen.centerX = app.screen.width / 2,
    screen.centerY = app.screen.height / 2

    app.stage.children.forEach( container => {
        container.children.forEach( element => {
            element.resize(screen)
        })
    })
}

export function initGame() {
    const loadingProgressBar = new ProgressBar( screen, "0%" )
    ControlUI.addChild( loadingProgressBar )

    loadAssets( (loadedAssetsCount, totalAssetsCount, loadingProgress) => {
        loadingProgressBar.upload(loadingProgress, `${loadingProgress}%`)
        if (loadedAssetsCount === totalAssetsCount) {
            clearContainer( ControlUI )

            smoothShowElement(BG)
            showStartMenu()
            BG.addChild( new Background( screen ) )
        }
    })
}

function smoothShowElement( element, callback = null ) {
    element.alpha = 0.001
    element.tick = (delta) =>{
        element.alpha += Math.sin(delta * element.alpha * 0.12)
        if (element.alpha >= 1) {
            element.alpha = 1
            tickerUpdateRemove( element )
            if (callback) callback()
        }
    }
    tickerUpdateAdd( element )
}

function smoothHideElement( element, callback = null ) {
    element.tick = (delta) =>{
        element.alpha -= Math.sin(delta * element.alpha * 0.12)
        if (element.alpha < 0.001) {
            element.alpha = 0
            tickerUpdateRemove( element )
            if (callback) callback()
        }
    }
    tickerUpdateAdd( element )
}

function showStartMenu() {
    smoothShowElement(ControlUI)
    smoothShowElement(TopUI)
    smoothShowElement(BottomUI)

    const awaitStartGame = () => {
        if (ControlUI.children.length === 0
        && TopUI.children.length === 0) {
            startGame()
        }
    }
    
    let isOnceStartClicked = false
    const startClicked = () => {
        if (isOnceStartClicked) return
        isOnceStartClicked = true

        smoothHideElement(ControlUI, () => {
            clearContainer(ControlUI)
            awaitStartGame()
        })

        smoothHideElement(TopUI, () => {
            clearContainer(TopUI)
            awaitStartGame()
        })

        // smoothHideElement(BottomUI)
    }

    const logoClicked = () => window.open('https://yandex.ru/games/developer?name=mars%20game', '_blank');

    const languageClicked = () => {
        isLangRu = !isLangRu
        startButton.setLanguage(isLangRu)
    }

    TopUI.addChild( new LogoButton( screen, logoClicked ) )
    TopUI.addChild( new LanguageButton( screen, isLangRu, languageClicked ) )

    const startButton = new StartButton( screen, isLangRu, startClicked )
    ControlUI.addChild( startButton )
        
    BottomUI.addChild( new SoundButton( screen ) )
    BottomUI.addChild( new ResultsButton( screen ) )
}

function startGame() {
    startMusic()

    let attempts = 3
    let scores = 0

    let isOnChoice = false

    const colors = [
        0x0000ff,
        0xff0000,
        0xffff00,
        0x00ff00,
        0x00ffff,
        0xff00ff,
    ]

    let colorIndex = Math.floor( Math.random() * colors.length )
    let side = Math.floor(Math.random() * 4)

    let slides = 2
    let levelSlides = 0

    let helps = 3

    const slideReady = () => {
        levelSlides++
        if (levelSlides < slides) setTimeout(addSlide, 600)
        else {
            levelSlides = 0
            smoothShowElement(ControlUI, () => isOnChoice = true)

            //setTimeout( () => controller.setHelp(0), 2400 )
        }
    }

    const addSlide = () => {
        SLIDES.addChild( new Slide(screen, colors[colorIndex], side, slideReady))

        const nextSide = Math.floor(Math.random() * 4)
        if (side === nextSide) side = (nextSide + 2) % 4
        else side = nextSide

        colorIndex++
        if (colorIndex === colors.length) colorIndex = 0
    }

    const callbackHideSlide = (slide) => {
        removeSprite( slide )
        isOnChoice = true
    }

    const removeSlide = (slideIndex) => {
        SLIDES.children[slideIndex].hide( callbackHideSlide )
    }

    const nextLevel = () => {
        addSlide()
    }

    const controllerClicked = (side) => {
        if (isOnChoice === false) return
        const slideIndex = SLIDES.children.length - 1
        const isOk = side === SLIDES.children[slideIndex].side
        console.log('controllerClicked', side, isOk)
        if (isOk) {
            isOnChoice = false
            scores++
            scoresUI.addScore(scores)
            removeSlide(slideIndex)
        } else {
            attempts--;
            attemptsUI.removeAttempt(attempts)
            setTimeout(() => {
                attempts++;
                attemptsUI.addAttempt(attempts)
            }, 2400)
        }
    }

    const scoresUI = new Scores( screen, scores )
    TopUI.addChild( scoresUI )

    const attemptsUI = new Attempts( screen, attempts )
    TopUI.addChild( attemptsUI )

    
    const controller = new Controller( screen, controllerClicked )
    ControlUI.addChild( controller )
    
    smoothShowElement(TopUI, () => setTimeout(nextLevel, 600))

    addEventListener('keyup', (key) => {
        if (isOnChoice === false) return
        switch(key.code) {
            case 'ArrowUp' : controllerClicked(0); break;
            case 'ArrowRight' : controllerClicked(1); break;
            case 'ArrowDown' : controllerClicked(2); break;
            case 'ArrowLeft' : controllerClicked(3); break;

            case 'KeyW' : controllerClicked(0); break;
            case 'KeyD' : controllerClicked(1); break;
            case 'KeyS' : controllerClicked(2); break;
            case 'KeyA' : controllerClicked(3); break;
        }
    })
}

let tickerUpdateArr = []

export function tickerUpdateAdd( element ) {
    tickerUpdateArr.push( element )
}

export function tickerUpdateRemove( element ) {
    tickerUpdateArr = tickerUpdateArr.filter( e => e !== element )
}

app.ticker.add( delta => {
    tickerUpdateArr.forEach( element => element.tick(delta) )
})