import { Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { playSound, musicOn, musicOff, musicCheck } from "./sound";

const sprite = {
    width: 383,
    height: 271,
    k: 383 / 271,
    screenK: 7,
    offsetK: 20
}

class SoundButton extends Sprite {
    constructor( screen ) {
        super( getTexture( musicCheck() ? textureKeys.musicOn : textureKeys.musicOff ) )
        this.anchor.set( 0, 1 )
        this.resize(screen)
        this.eventMode = 'static';
        this.on('pointertap', () => {
            if (musicCheck()) {
                musicOff()
                this.texture = getTexture(textureKeys.musicOff)
            } else {
                musicOn()
                playSound(soundKeys.click)
                this.texture = getTexture(textureKeys.musicOn)
            }
        })
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const size = screenSize / sprite.screenK
        this.height = size > sprite.height ? sprite.height : size
        this.width = this.height * sprite.k

        this.position.x = screenSize / sprite.offsetK
        this.position.y = screen.height - screenSize / sprite.offsetK
    }
}

export default SoundButton