import { Graphics } from 'pixi.js'
import { tickerUpdateAdd, tickerUpdateRemove } from './game'
import { soundKeys } from "./assets";
import { playSound } from "./sound";

class Slide extends Graphics {
    constructor (screen, color, side, slideReady) {
        super()
        this.screen = screen
        this.color = color
        this.side = side
        this.sizeScale = 0
        this.isOnOpen = true
        this.resize(screen)
        this.speed = 0.021
        this.readyCallback = slideReady
        tickerUpdateAdd(this)
        playSound(soundKeys.slide)
    }

    resize(screen) {
        this.screen = screen
        this.clear()

        this.beginFill(this.color, 1)
        this.drawRect(0, 0, screen.width, screen.height)
        this.endFill()
        console.log(this.side)

        if(this.side % 2 === 0) {
            // if top or down
            this.height = this.screen.height * this.sizeScale
            if (this.side === 2) this.position.y = this.screen.height - this.height
        } else {
            // if right or left
            this.width = this.screen.width * this.sizeScale
            if (this.side === 1) this.position.x = this.screen.width - this.width
        }
    }

    change( scale ) {
        this.sizeScale += scale * this.speed

        if (this.sizeScale >= 1) {
            this.isOnOpen = false
            this.sizeScale = 1
            tickerUpdateRemove(this)
            this.readyCallback()
        }

        if (this.sizeScale <= 0) {
            this.sizeScale = 0
            tickerUpdateRemove(this)
            this.readyCallback(this)
            return
        }

        if(this.side % 2 === 0) {
            // if top or down
            this.height = this.screen.height * this.sizeScale
            if (this.side === 2) this.position.y = this.screen.height - this.height
        } else {
            // if right or left
            this.width = this.screen.width * this.sizeScale
            if (this.side === 1) this.position.x = this.screen.width - this.width
        }
    }

    hide(callback) {
        console.log('close')
        this.readyCallback = callback
        tickerUpdateAdd(this)
    }

    tick( delta ) {
        if (this.isOnOpen) this.change(delta)
        else this.change(-delta)
    }
}

export default Slide