import { Container, Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { tickerUpdateAdd, tickerUpdateRemove } from './game'
import { playSound } from "./sound";

const settings = {
    size: 812,
    halfSize: 406,
    screenK: 2,

    upAnchor:    {x: 1, y: 1},
    rightAnchor: {x: 0, y: 1},
    downAnchor:  {x: 0, y: 0},
    leftAnchor:  {x: 1, y: 0},
}

class Controller extends Container {
    constructor( screen, clickCallback ) {
        super()
        this.clickCallback = clickCallback
        this.up = new Sprite( getTexture(textureKeys.arrowUp) )
        this.right = new Sprite( getTexture(textureKeys.arrowRight) )
        this.down = new Sprite( getTexture(textureKeys.arrowDown) )
        this.left = new Sprite( getTexture(textureKeys.arrowLeft) )
        this.up.anchor.set(settings.upAnchor.x, settings.upAnchor.y)
        this.right.anchor.set(settings.rightAnchor.x, settings.rightAnchor.y)
        this.down.anchor.set(settings.downAnchor.x, settings.downAnchor.y)
        this.left.anchor.set(settings.leftAnchor.x, settings.leftAnchor.y)
        this.up.eventMode = 'static'
        this.right.eventMode = 'static'
        this.down.eventMode = 'static'
        this.left.eventMode = 'static'
        this.up.on('pointertap', () => this.clickCallback(0) )
        this.right.on('pointertap', () => this.clickCallback(1) )
        this.down.on('pointertap', () => this.clickCallback(2) )
        this.left.on('pointertap', () => this.clickCallback(3) )
        this.up.isActive = false
        this.right.isActive = false
        this.down.isActive = false
        this.left.isActive = false
        this.addChild(this.up, this.right, this.down, this.left)
        this.resize(screen)
        this.rotation = Math.PI / 4

        this.help = null
        this.isAddAlpha = false
        this.stepAlpha = 0.009
    }

    resize(screen) {
        const size = screen.width > screen.height ? screen.height / settings.screenK : screen.width / settings.screenK
        const scale = (size > settings.size) ? 1 : size / settings.size
        this.position.x = screen.centerX
        this.position.y = screen.centerY

        this.up.width = this.up.height = settings.halfSize * scale
        this.right.width = this.right.height = settings.halfSize * scale
        this.down.width = this.down.height = settings.halfSize * scale
        this.left.width = this.left.height = settings.halfSize * scale

        this.up.position.x = 0
        this.up.position.y = 0

        this.right.position.x = 0
        this.right.position.y = 0

        this.down.position.x = 0
        this.down.position.y = 0

        this.left.position.x = 0
        this.left.position.y = 0
    }

    setHelp( side ) {
        switch(side) {
            case 0 : this.help = this.up; break;
            case 1 : this.help = this.right; break;
            case 2 : this.help = this.down; break;
            case 3 : this.help = this.left; break;
            default: return
        }
        this.isAddAlpha = true
        tickerUpdateAdd(this)
        console.log('this.help', this.help)
    }

    tick( delta ) {
        if(this.isAddAlpha) {
            this.help.alpha -= this.stepAlpha * delta
            if (this.help.alpha < 0.55) this.isAddAlpha = false
        } else {
            this.help.alpha += this.stepAlpha * delta
            if (this.help.alpha > 1) {
                /*
                this.help = null
                tickerUpdateRemove(this)
                */
                this.isAddAlpha = true
            }
        }
    }
}

export default Controller