import { TextStyle } from "pixi.js"
import { fontKeys } from "./assets"

export const textStyles = {
    loading: new TextStyle({
        fontFamily: fontKeys.CyrillicRevue,
        fontSize: 24,
        fill: '#000000',
        align: 'center'
    }),

    scores: new TextStyle({
        fontFamily: fontKeys.CyrillicRevue,
        fontSize: 32,
        fill: '#000000',
        align: 'left'
    }),

    attempts: new TextStyle({
        fontFamily: fontKeys.CyrillicRevue,
        fontSize: 32,
        fill: '#000000',
        align: 'right'
    }),

    gradientText: new TextStyle({
        fontFamily: fontKeys.CyrillicRevue,
        fontSize: 32,
        fill: ['#000000', '#ff0064', '#000000'],

        dropShadow: true,
        dropShadowColor: '#ffffff',
        dropShadowBlur: 6,
        dropShadowAngle: 0,
        dropShadowDistance: 0,

        wordWrap: true,
        wordWrapWidth: 400,
    }),

    textWithShadow: new TextStyle({
        fontFamily: fontKeys.CyrillicRevue,
        fontSize: 18,
        fontStyle: 'normal',
        fontWeight: 'normal',
        fill: ['#ff0000', '#ffff00'],
        /*
        stroke: '#ffffff',
        strokeThickness: 2,
        */
        /*
        dropShadow: true,
        dropShadowColor: '#ff00ff',
        dropShadowBlur: 3,
        dropShadowDistance: 4,
        */
        wordWrap: true,
        wordWrapWidth: 440,
        lineJoin: 'round',
    }),
}