import { Container, Sprite, Text } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { textStyles } from './textStyles'
import { playSound } from "./sound";

const settings = {}
settings.imageWidth = 211
settings.imageHeight = 251
settings.imageK = settings.imageWidth / settings.imageHeight
settings.imageScreenK = 8
settings.imageOffsetK = 20

settings.textScreenK = 10
settings.textSize = (settings.imageHeight * settings.imageScreenK) / settings.textScreenK

class Attempts extends Container {
    constructor( screen, attempts ) {
        super()
        this.image = new Sprite( getTexture(textureKeys.attempts) )
        this.image.anchor.set( 1, 0 )
        this.addChild(this.image)
        this.text = new Text( attempts, {...textStyles.attempts, fontSize: settings.textSize} )
        this.text.anchor.set( 1, 0 )
        this.addChild(this.text)
        this.resize(screen)
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const spriteSize = screenSize / settings.imageScreenK
        
        this.image.height = spriteSize > settings.imageHeight ? settings.imageHeight : spriteSize
        this.image.width = spriteSize > settings.imageHeight ? settings.imageWidth : this.image.height * settings.imageK
        this.image.position.x = screen.width - screenSize / settings.imageOffsetK
        this.image.position.y = screenSize / settings.imageOffsetK

        const textSize = screenSize / settings.textScreenK
        this.text.style.fontSize = textSize > settings.textSize ? settings.textSize : textSize
        const textOffset = (this.image.height - textSize) / 1.5
        this.text.position.x = this.image.position.x - textOffset - this.image.width
        this.text.position.y = textOffset + this.image.position.y
    }

    addAttempt(attempts) {
        playSound(soundKeys.clear)
        this.text.text = attempts
    }

    removeAttempt(attempts) {
        playSound(soundKeys.false)
        this.text.text = attempts
    }
}

export default Attempts