import { musics, sounds } from "./loader"
const { Howl } = require('howler')

let isMusic = true
let isOnGame = false;

let musicKeys = Array.from( musics.keys() );
let musicIndex = 0
let music = null

export function musicOn() {
    isMusic = true
    playMusic()
}

export function musicOff() {
    isMusic = false
    if (music) stopMusic()
}

export function musicCheck() {
    return isMusic
}

export function playSound(sound) { console.log(sound)
    if (!isMusic) return
    if (sounds.has(sound)) sounds.get(sound).play()
}

function nextTrack() {
    music = null
    musicIndex++
    if (musicIndex === musicKeys.length) musicIndex = 0

    playMusic()
}

export function startMusic() {
    isOnGame = true
    playMusic()
}

export function playMusic() {
    if (!isMusic || !isOnGame) return

    if (music) return music.play()

    const track = musics.get(musicKeys[musicIndex])
    music = new Howl({
        src: [track],
        volume: 0.5,
        onend: nextTrack,
    });

    music.play()
}

export function stopMusic() {
    if (music) music.pause()
}