import { Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { playSound } from "./sound";

const sprite = {
    width: 317,
    height: 265,
    k: 317 / 265,
    screenK: 7,
    offsetK: 20
}

class ResultsButton extends Sprite {
    constructor( screen ) {
        super( getTexture( textureKeys.results ) )
        this.anchor.set( 1, 1 )
        this.resize(screen)
        this.eventMode = 'static';
        this.on('pointertap', () => {
            playSound(soundKeys.click)
            console.log('RESULTS')
        })
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const size = screenSize / sprite.screenK
        this.height = size > sprite.height ? sprite.height : size
        this.width = this.height * sprite.k

        this.position.x = screen.width - screenSize / sprite.offsetK
        this.position.y = screen.height - screenSize / sprite.offsetK
    }
}

export default ResultsButton