import { Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { playSound } from "./sound";

const sprite = {
    width: 866,
    height: 380,
    k: 866 / 380,
}

class StartButton extends Sprite {
    constructor( screen, isLangRu, clickCallback ) {
        super( getTexture( isLangRu ? textureKeys.startRu : textureKeys.startEn ) )
        this.anchor.set( 0.5 )
        this.resize(screen)
        this.eventMode = 'static';
        this.clickCallback = clickCallback
        this.isClicked = false
        this.on('pointertap', () => {
            if (!this.isClicked) {
                this.isClicked = true
                playSound(soundKeys.start)
                this.clickCallback()
            }
        })
    }

    resize(screen) {
        this.position.x = screen.centerX
        this.position.y = screen.centerY
        const size = screen.width > screen.height ? screen.height : screen.width
        this.width = sprite.width < size / 2 ? sprite.width : size / 2
        this.height = this.width / sprite.k
    }

    setLanguage(isLangRu) {
        this.texture = getTexture( isLangRu ? textureKeys.startRu : textureKeys.startEn )
    }
}

export default StartButton