import { Sprite } from "pixi.js"
import { textureKeys, soundKeys } from "./assets";
import { getTexture } from "./loader";
import { playSound } from "./sound";

const sprite = {
    width: 276,
    height: 276,
    screenK: 8,
    offsetK: 20
}

class LanguageButton extends Sprite {
    constructor( screen, isLangRu, clickCallback ) {
        super( getTexture( isLangRu ? textureKeys.langRu : textureKeys.langEn ) )
        this.isLangRu = isLangRu
        this.anchor.set( 1, 0 )
        this.resize(screen)
        this.eventMode = 'static';
        this.clickCallback = clickCallback
        this.on('pointertap', () => {
            playSound(soundKeys.click)
            this.changeLanguage()
            this.clickCallback()
        })
    }

    resize(screen) {
        const screenSize = screen.width > screen.height ? screen.height : screen.width
        const size = screenSize / sprite.screenK
        if (size > sprite.width) {
            this.width = sprite.width
            this.height = sprite.height
        } else {
            this.width = size
            this.height = size
        }
        this.position.x = screen.width - screenSize / sprite.offsetK
        this.position.y = screenSize / sprite.offsetK
    }

    changeLanguage() {
        this.isLangRu = !this.isLangRu
        this.texture = getTexture( this.isLangRu ? textureKeys.langRu : textureKeys.langEn )
    }
}

export default LanguageButton